export default {
	props: {
		tabList: {
			type: Array,
			default: ()=>{
				return []
			}
		},
		tabActiveIdx: {
			type: Number,
			default: 0
		},
		isScroll: {
			type: Boolean,
			default: false
		},
		callback:{
			type: Function
		}
	},
	data() {
		return {
			tabIdx: 0,
			scrollLeft: 0
		};
	},
	watch: {
		tabActiveIdx(newValue, oldValue) {
			console.log('newValue', newValue);
			this.tabSelect(newValue);
		}
	},
	methods: {
		tabSelect(idx) {
			console.log('idx:::',idx)
			this.tabIdx = idx;
			this.scrollLeft = idx * 30;
			//this.$emit('tabSelect', idx);
			if(this.callback){
				this.callback(idx)
			}
		}
	}
};
